@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Open+Sans:wght@400;700&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}#root{margin-top:70px;margin-bottom:50px}html,body #root{height:100%;min-height:100%;background-color:#e6e6e6}.portfolio{padding:40px 0}.portfolio__row{display:flex}.portfolio__col{margin-bottom:45px}.portfolio__wrapper{display:flex;flex-direction:column;text-align:center;margin:0 auto;height:100%;width:100%;padding:7px;border-radius:1%;background-color:#fff}@media (min-width: 768px){.portfolio__wrapper{width:90%}.portfolio__wrapper:hover .portfolio__img-div{opacity:0.5;-webkit-filter:grayscale(0.8);filter:grayscale(0.8)}.portfolio__wrapper:hover .portfolio__link-div{opacity:1;top:30%;left:50%;transform:translate(-50%);transition:all 0.5s}}@media (min-width: 992px){.portfolio__wrapper{padding:10px}}.portfolio__wrapper--right{box-shadow:8px -4px 7px 3px #a6a6a6}.portfolio__wrapper--right:hover{transform:scale(1.1);transition:all 0.5s;box-shadow:-8px 4px 7px 3px #a6a6a6}.portfolio__wrapper--left{box-shadow:-8px -4px 7px 3px #a6a6a6}.portfolio__wrapper--left:hover{transform:scale(1.1);transition:all 0.5s;box-shadow:8px 4px 7px 3px #a6a6a6}.portfolio__wrapper--prepare{justify-content:center}.portfolio__img-div{position:relative;margin-bottom:25px;box-shadow:0px 10px 28px 0px #261B1C}.portfolio__img{width:100%;height:100%}.portfolio__title{color:#352627}.portfolio__description{padding:0  15px;text-align:justify;color:#352627}.portfolio__bold{font-weight:700}.portfolio__link-div{text-align:center;margin:15px 0;opacity:1}@media (min-width: 768px){.portfolio__link-div{position:absolute;opacity:0;margin:0;top:55%;left:50%;transform:translate(-50%)}}.portfolio__link{text-transform:uppercase;border-radius:5%;padding:5px 15px;margin:15px;box-shadow:3px 3px 2px #a6a6a6;color:#fff;background-color:#533b3d}.portfolio__link:hover{text-decoration:none;color:#ff3f34;box-shadow:3px 3px 2px #ff3f34}

